.title {
  font-family: 'Poppins';
  font-style: normal;
  font-weight: 400;
  font-size: 30px;
  line-height: 1.5;

  padding-bottom: 20px;
  // padding-left: 15px;

  @include mq(mob-only) {
    margin-top: 28px;
    padding-bottom: 8px;
  }

  @include mq(desk) {
    line-height: 1;
    padding-left: 15px;
    position: absolute;
    top: 120px;
  }
}

.mainBox {
  @include mq(mob-only) {
    display: flex;
    flex-direction: column;
    gap: 65px;
    align-items: stretch;
  }
  @include mq(tab) {
    display: flex;
    gap: 32px;
  }
  @include mq(desk) {
    display: flex;
    gap: 32px;
    // position: absolute;
    top: 175px;
    padding-top: 53px;
  }
}

.doughnut {
  position: relative;
  @include mq(mob-only) {
    max-width: 440px;
    max-height: 440px;

    display: flex;
    justify-content: center;
  }

  @include mq(tab) {
    width: 336px;
    height: 336px;
  }
  @include mq(desk) {
    width: 288px;
    height: 288px;
  }
}

.doughnutMonth {
  position: absolute;
  top: 50%;
  right: 50%;
  transform: translate(50%, -50%);
  font-family: 'Circe';
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 1.5;
  color: var(--text-main-color);
}

.doughnutMonthNoValue {
  border-radius: 50%;
  border: 43px solid var(--border-doughnut);
  display: flex;
  font-family: 'Circe';
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 1.5;
  align-items: center;
  justify-content: center;

  @include mq(mob-only) {
    width: 100%;
    aspect-ratio: 1 / 1;
  }

  @include mq(tab) {
    width: 336px;
    height: 336px;
  }
  @include mq(desk) {
    width: 288px;
    height: 288px;
  }
}

.selectItem {
  cursor: pointer;
  /* Reset */
  appearance: none;
  border: 0;
  outline: 0;
  font: inherit;

  @include mq(mob-only) {
    min-width: 280px;

    height: 50px;
    padding-left: 20px;
  }
  @include mq(tab) {
    width: 160px;
    height: 50px;
    padding-left: 16px;
  }
  @include mq(desk) {
    width: 182px;
    height: 50px;
    padding-left: 20px;
  }

  border: 1px solid var(--text-main-colo);
  border-radius: 30px;

  font-family: 'Circe';
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: var(--text-main-colo);
  background: transparent;
}

.selectOption {
  background: var(--background-color-dropdown);
  box-shadow: var(--shadow-dropdown);
  backdrop-filter: var(--filter-dropdown);
  /* Note: backdrop-filter has minimal browser support */
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  // background-color: $select-background;
  padding: 8px 15px;

  border-radius: 20px;
  &:hover {
    outline: none;
    background: var(--text-main-color);
  }
}
.select {
  padding-bottom: 20px;
  display: flex;
  justify-content: space-between;
  border-radius: 30px;
  position: relative;
  @include mq(mob-only) {
    min-width: 280px;
    gap: 20px;
    flex-direction: column;
  }
  @include mq(tab) {
    width: 336px;
    gap: 16px;
    flex-direction: row;
  }
  @include mq(desk) {
    width: 395px;
    gap: 32px;
  }
}

.selectClosed {
  filter: var(--filter-dropdown-display-none);
  pointer-events: none;
}

.statisticData {
  position: relative;
  @include mq(mob-only) {
    display: flex;
    flex-direction: column;
  }
  @include mq(tab) {
    bottom: 45px;
  }
  @include mq(desk) {
    bottom: 0;
  }
}

.table {
  border-spacing: 0;
  border-collapse: collapse;
  align-items: stretch;
  overflow: hidden;
  padding: 0;

  @include mq(mob-only) {
    min-width: 280px;
    height: 52px;
  }
  @include mq(tab) {
    width: 336px;
    height: 50px;
  }
  @include mq(desk) {
    width: 395px;
  }
}

.thead {
  border-spacing: 0;
  background: var(--text-white-color);
  height: 58px;
  padding: 0;
  border: none;

  :first-child {
    border-radius: 30px 0 0 30px;
    text-align: left;

    @include mq(mob-only) {
      padding-left: 20px;
      height: 58px;
    }
    @include mq(tab) {
      padding-left: 20px;
      height: 58px;
    }
    @include mq(desk) {
      padding-left: 28px;
      height: 58px;
    }
  }
  :last-child {
    border-radius: 0 30px 30px 0;
    text-align: right;
    @include mq(mob-only) {
      padding-right: 20px;
      height: 58px;
    }
    @include mq(tab) {
      padding-right: 20px;
    }
    @include mq(desk) {
      padding-right: 28px;
    }
  }
}

.tbody {
