.textAllTransaction {
  display: flex;
  gap: 10px;
  align-items: center;
}

.allTransactionText {
  transition: all 0.2s linear 0s;

  display: inline-block;
  position: relative;

  &:after {
    content: '';
    position: absolute;
    width: 100%;
    transform: scaleX(0);
    height: 2px;
    bottom: 0;
    left: 0;
    background-color: var(--btn-blue-color-hover);
    transform-origin: bottom right;
    transition: transform 0.25s cubic-bezier(0.4, 0, 0.2, 1);
  }
  &:hover:after {
    transform: scaleX(1);
    transform-origin: bottom left;
  }
}

.squareAndText {
  // position: absolute;
  display: flex;
  align-items: center;
  // justify-content: center;
  // border: 0;
  vertical-align: middle;
}

.textCategory {
  display: inline-block;
  font-family: 'Circe';
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.125;

  @include mq(mob-only) {
    width: 110px;
    overflow-y: hidden;

    &::-webkit-scrollbar {
      width: 4px;
      height: 4px;
    }

    &::-webkit-scrollbar-track {
      -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
      border-radius: 10px;
    }

    &::-webkit-scrollbar-thumb {
      border-radius: 10px;
      -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.5);
    }
  }
  @include mq(tab) {
    width: 110px;
    overflow-y: hidden;

    &::-webkit-scrollbar {
      width: 4px;
      height: 4px;
    }

    &::-webkit-scrollbar-track {
      -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
      border-radius: 10px;
    }

    &::-webkit-scrollbar-thumb {
      border-radius: 10px;
      -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.5);
    }
  }
}

